import {
  FormGroup,
  Label,
  Input,
  Anchor,
  Button,
  Alert,
  Spinner,
} from '@sieloapp/components';
import { useForm, SubmitHandler } from 'react-hook-form';
import { hasKey } from '@sieloapp/shared/methods';
import {
  StyledWrap,
  StyledTitle,
  StyledDesc,
  StyledLabelWrap,
  StyledRedirect,
} from './style';
// AWS Amplify
import { Auth } from 'aws-amplify';
// Redux
import { useDispatch } from 'react-redux';
import { toggleUser } from '../../redux/slices/user';
import { useNavigate, Link } from 'react-router-dom';
import { useMemo, useState } from 'react';
import xorCipher from '../../../src/helpers/crypt';
import {
  CognitoUser,
  CognitoUserPool,
  ICognitoUserData,
} from 'amazon-cognito-identity-js';

interface IFormValues {
  email: string;
  password: string;
}

const SigninForm = () => {
  const [msg, setMessage] = useState<React.ReactNode>('');
  const [signingIn, setsigningIn] = useState(false);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const userPoolAWS = process.env.REACT_APP_COGNITO_USER_POOL_ID as string;
  const clientIdAWS = process.env.REACT_APP_COGNITO_CLIENT_ID as string;
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<IFormValues>();

  // const poolData = useMemo(
  //   () => ({
  //     UserPoolId: userPoolAWS,
  //     ClientId: clientIdAWS,
  //   }),
  //   [userPoolAWS, clientIdAWS],
  // );

  const onSubmit: SubmitHandler<IFormValues> = async (data) => {
    setsigningIn(true);
    setMessage('');
    try {
      const currentUser = await Auth.signIn({
        username: data.email,
        password: data.password,
      });
      setsigningIn(false);
      dispatch(
        toggleUser({
          userName: currentUser.username,
          email: currentUser.attributes.email,
          firstName: currentUser.attributes.name,
          lastName: currentUser.attributes.family_name,
          role: 'user',
          token: currentUser.attributes.family_name,
          company: 0,
          verified: true,
          clientId: clientIdAWS,
          userPoolId: userPoolAWS,
          password: undefined,
        }),
      );
      navigate('/');
    } catch (error) {
      setsigningIn(false);
      const typedError = error as Error;
      if (typedError.message === 'User is not confirmed.') {
        setMessage(
          <>
            {typedError.message} Verify your account{' '}
            <a href="/verify-account">here!</a>
          </>,
        );
        dispatch(
          toggleUser({
            userName: data.email,
            email: data.email,
            firstName: undefined,
            lastName: undefined,
            role: 'user',
            token: undefined,
            company: 0,
            verified: false,
            clientId: clientIdAWS,
            userPoolId: userPoolAWS,
            password: xorCipher(data.password),
          }),
        );
      } else {
        setMessage(typedError.message);
      }
    }
  };

  return (
    <StyledWrap>
      <StyledTitle>Sign In</StyledTitle>
      <StyledDesc>Welcome back! Please signin to continue.</StyledDesc>
      {msg !== '' && <Alert color={'danger'}>{msg}</Alert>}
      <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
        <FormGroup mb="20px">
          <Label display="block" mb="5px" htmlFor="email">
            Email address
          </Label>
          <Input
            type="email"
            id="email"
            placeholder="yourname@yourmail.com"
            feedbackText={errors?.email?.message}
            state={hasKey(errors, 'email') ? 'error' : 'success'}
            showState={!!hasKey(errors, 'email')}
            {...register('email', {
              required: 'Email is required',
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                message: 'invalid email address',
              },
            })}
          />
        </FormGroup>
        <FormGroup mb="20px">
          <StyledLabelWrap>
            <Label display="block" mb="5px" htmlFor="password">
              Password
            </Label>
            <Anchor path="/forgot-password" fontSize="13px">
              Forgot password?
            </Anchor>
          </StyledLabelWrap>
          <Input
            id="password"
            type="password"
            placeholder="Enter your password"
            feedbackText={errors?.password?.message}
            state={hasKey(errors, 'password') ? 'error' : 'success'}
            showState={!!hasKey(errors, 'password')}
            {...register('password', {
              required: 'Password is required',
              minLength: {
                value: 8,
                message: 'Minimum length is 8',
              },
              maxLength: {
                value: 100,
                message: 'Maximum length is 100',
              },
            })}
          />
        </FormGroup>
        <Button type="submit" color="brand2" fullwidth>
          {signingIn ? (
            <div className="spin">
              <Spinner size="xs" color="light" />
            </div>
          ) : (
            'Sign In'
          )}
        </Button>
      </form>
      <StyledRedirect>
        Don't have an account? Please <Link to="/signup">Sign Up here</Link>
      </StyledRedirect>
    </StyledWrap>
  );
};

export default SigninForm;
